/* NewsArticle Page Styles */
.news-article-page {
  background: #000000;
  color: #ffffff;
  font-family: 'Inter', 'Segoe UI', 'Roboto', Arial, sans-serif;
  min-height: 100vh;
  line-height: 1.7;
}

/* Container */
.article-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 2rem;
}

/* Breadcrumb */
.article-breadcrumb {
  background: #0a0a0a;
  padding: 1.5rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.breadcrumb-nav {
  display: flex;
  align-items: center;
}

.breadcrumb-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: #57bceb;
  font-weight: 600;
  font-size: 1rem;
  background: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0.5rem 0;
}

.breadcrumb-link:hover {
  color: #ffffff;
}

.breadcrumb-icon {
  width: 16px;
  height: 16px;
  transition: transform 0.3s ease;
}

.breadcrumb-link:hover .breadcrumb-icon {
  transform: translateX(-2px);
}

/* Hero Image */
.article-hero {
  background: #000000;
  padding: 0 0 3rem 0;
}

.article-hero-image-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 2rem;
}

.article-hero-image {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 1rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* MOLIT Award image - show full photo */
.molit-award-image .article-hero-image {
  object-fit: contain;
  object-position: center;
  background: #1a1a1a;
}

/* Article Main */
.article-main {
  background: #000000;
  padding: 0 0 4rem 0;
}

/* Article Header */
.article-header {
  padding: 2rem 0 3rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  margin-bottom: 3rem;
}

.article-category-badge {
  display: inline-block;
  padding: 0.6rem 1.2rem;
  border-radius: 25px;
  font-size: 0.9rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 1.5rem;
  border: 1px solid;
  backdrop-filter: blur(10px);
}

/* Category colors matching News page - bright backgrounds */
.article-category-badge[data-category*="Company"],
.article-category-badge[data-category*="회사"] {
  background: rgba(230, 245, 255, 0.98);
  color: #0d47a1;
  border-color: rgba(100, 181, 246, 0.9);
}

.article-category-badge[data-category*="Business"],
.article-category-badge[data-category*="비즈니스"] {
  background: rgba(230, 255, 230, 0.98);
  color: #1b5e20;
  border-color: rgba(129, 199, 132, 0.9);
}

.article-category-badge[data-category*="Technology"],
.article-category-badge[data-category*="기술"] {
  background: rgba(224, 247, 250, 0.98);
  color: #01579b;
  border-color: rgba(129, 212, 250, 0.9);
}

.article-category-badge[data-category*="Events"],
.article-category-badge[data-category*="이벤트"] {
  background: rgba(255, 243, 224, 0.98);
  color: #e65100;
  border-color: rgba(255, 183, 77, 0.9);
}

.article-category-badge[data-category*="Conference"],
.article-category-badge[data-category*="학술대회"] {
  background: rgba(243, 229, 245, 0.98);
  color: #4a148c;
  border-color: rgba(186, 104, 200, 0.9);
}

.article-category-badge[data-category*="Internship"],
.article-category-badge[data-category*="인턴십"] {
  background: rgba(255, 251, 230, 0.98);
  color: #f57f17;
  border-color: rgba(255, 213, 79, 0.9);
}

.article-category-badge[data-category*="Award"],
.article-category-badge[data-category*="수상"],
.article-category-badge[data-category*="상"] {
  background: rgba(255, 235, 238, 0.98);
  color: #c62828;
  border-color: rgba(239, 83, 80, 0.9);
}

.article-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  line-height: 1.2;
  margin-bottom: 2rem;
  letter-spacing: -0.02em;
}

.article-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  align-items: center;
}

.article-meta-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.95rem;
  font-weight: 500;
}

.meta-icon {
  width: 18px;
  height: 18px;
  color: #57bceb;
  flex-shrink: 0;
}

.article-date,
.article-author,
.article-read-time {
  color: rgba(255, 255, 255, 0.6);
}

/* Article Body */
.article-body {
  max-width: 700px;
  margin: 0 auto;
}

.article-content {
  font-size: 1.1rem;
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.85);
  margin-bottom: 3rem;
}

.article-content h2 {
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
  margin: 2.5rem 0 1.5rem 0;
  line-height: 1.3;
}

.article-content h3 {
  font-size: 1.4rem;
  font-weight: 600;
  color: #ffffff;
  margin: 2rem 0 1rem 0;
  line-height: 1.4;
}

.article-content p {
  margin-bottom: 1.5rem;
}

.article-content strong {
  color: #ffffff;
  font-weight: 700;
}

.article-content em {
  color: #57bceb;
  font-style: italic;
}

.article-content ul,
.article-content ol {
  margin: 1.5rem 0;
  padding-left: 2rem;
}

.article-content li {
  margin-bottom: 0.5rem;
}

.article-content blockquote {
  border-left: 4px solid #57bceb;
  background: rgba(255, 255, 255, 0.05);
  padding: 1.5rem 2rem;
  margin: 2rem 0;
  font-style: italic;
  color: rgba(255, 255, 255, 0.7);
  border-radius: 0 0.5rem 0.5rem 0;
}

/* Article Images */
.article-images {
  margin: 3rem 0;
}

.article-figure {
  margin: 2rem 0;
  text-align: center;
}

.article-image {
  width: 100%;
  max-width: 100%;
  height: auto;
  border-radius: 0.75rem;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 1rem;
}

.article-image-caption {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
  line-height: 1.5;
}

/* Article Video */
.article-video {
  margin: 3rem 0;
}

.video-container {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
  height: 0;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.youtube-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 0;
}

/* Training Programs Section with Inline Image */
.training-programs-section {
  display: flex;
  gap: 2rem;
  margin: 2rem 0;
  align-items: flex-start;
}

.training-content {
  flex: 1;
}

.training-image {
  flex: 0 0 300px;
  text-align: center;
}

.inline-training-image {
  width: 100%;
  max-width: 300px;
  height: auto;
  border-radius: 0.75rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: transform 0.3s ease;
}

.inline-training-image:hover {
  transform: scale(1.02);
}

/* Side-by-side content with image */
.content-with-image-section {
  display: flex;
  gap: 3rem;
  margin: 2rem 0;
  align-items: flex-start;
}

.content-text {
  flex: 1;
}

.content-image {
  flex: 0 0 280px;
  text-align: center;
}

.inline-content-image {
  width: 100%;
  max-width: 280px;
  height: auto;
  border-radius: 0.75rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: transform 0.3s ease;
}

.inline-content-image:hover {
  transform: scale(1.02);
}

/* Responsive layout for side-by-side sections */
@media (max-width: 768px) {
  .training-programs-section,
  .content-with-image-section {
    flex-direction: column;
  }

  .training-image,
  .content-image {
    flex: 1;
    max-width: 100%;
  }

  .inline-training-image,
  .inline-content-image {
    max-width: 100%;
  }
}

/* Responsive design for training section */
@media (max-width: 768px) {
  .training-programs-section {
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .training-image {
    flex: none;
    order: -1; /* Put image before content on mobile */
  }
  
  .inline-training-image {
    max-width: 100%;
  }
}

/* Certificate Gallery */
.certificate-gallery {
  margin: 4rem 0;
  padding: 2rem 0;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.certificate-gallery-title {
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  margin-bottom: 2.5rem;
}

.certificate-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin: 0 auto;
  max-width: 100%;
}

.certificate-figure {
  text-align: center;
}

.certificate-image {
  width: 100%;
  max-width: 100%;
  height: 250px;
  object-fit: contain;
  object-position: center;
  border-radius: 0.5rem;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: transform 0.3s ease;
  margin-bottom: 1rem;
  background: rgba(255, 255, 255, 0.02);
}

.certificate-image:hover {
  transform: scale(1.02);
}

.certificate-caption {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  font-weight: 600;
  text-align: center;
}

/* Team CTA Button */
.team-cta {
  text-align: center;
  margin-top: 2rem;
}

.team-cta-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  background: transparent;
  color: #57bceb;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: none;
  font-family: inherit;
}

.team-cta-button:hover {
  color: #ffffff;
  transform: translateX(4px);
}

.team-cta-arrow {
  width: 16px;
  height: 16px;
  transition: transform 0.3s ease;
}

.team-cta-button:hover .team-cta-arrow {
  transform: translateX(2px);
}

/* Responsive Design for Certificates */
@media (max-width: 900px) {
  .certificate-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
  
  .certificate-image {
    height: 220px;
  }
}

@media (max-width: 600px) {
  .certificate-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .certificate-gallery-title {
    font-size: 1.5rem;
    margin-bottom: 2rem;
  }
  
  .certificate-image {
    height: 200px;
  }
}

/* Article Footer */
.article-footer {
  padding: 3rem 0 0 0;
  text-align: center;
}

.back-to-news-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2rem;
  background: transparent;
  color: #57bceb;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 0.75rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.back-to-news-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border-color: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

.back-icon {
  width: 18px;
  height: 18px;
  transition: transform 0.3s ease;
}

.back-to-news-btn:hover .back-icon {
  transform: translateX(-2px);
}

/* Article Not Found */
.article-not-found {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 1rem;
  margin: 4rem 0;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.article-not-found h1 {
  font-size: 2rem;
  color: #ffffff;
  margin-bottom: 1rem;
}

.article-not-found p {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 2rem;
}

/* Responsive Design */

/* Tablet */
@media (max-width: 900px) {
  .article-container {
    padding: 0 1.5rem;
  }
  
  .article-hero-image-container {
    padding: 0 1.5rem;
  }
  
  .article-hero-image {
    height: 300px;
  }
  
  .article-title {
    font-size: 2rem;
  }
  
  .article-meta {
    gap: 1.5rem;
  }
  
  .article-content {
    font-size: 1.05rem;
  }
  
  .article-content h2 {
    font-size: 1.6rem;
  }
  
  .article-content h3 {
    font-size: 1.3rem;
  }
}

/* Mobile */
@media (max-width: 600px) {
  .article-container {
    padding: 0 1rem;
  }
  
  .article-hero-image-container {
    padding: 0 1rem;
  }
  
  .article-hero-image {
    height: 250px;
    border-radius: 0.75rem;
  }
  
  .article-breadcrumb {
    padding: 1rem 0;
  }
  
  .breadcrumb-link {
    font-size: 0.9rem;
  }
  
  .article-title {
    font-size: 1.75rem;
    line-height: 1.3;
  }
  
  .article-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  
  .article-meta-item {
    font-size: 0.9rem;
  }
  
  .meta-icon {
    width: 16px;
    height: 16px;
  }
  
  .article-content {
    font-size: 1rem;
  }
  
  .article-content h2 {
    font-size: 1.4rem;
  }
  
  .article-content h3 {
    font-size: 1.2rem;
  }
  
  .article-content blockquote {
    padding: 1rem 1.5rem;
  }
  
  .back-to-news-btn {
    padding: 0.875rem 1.5rem;
    font-size: 0.95rem;
  }
}

/* Reduce spacing for intro paragraph before CTA */
.article-content:has(+ .article-cta) p:last-child {
  margin-bottom: 0.25rem;
}

/* Article CTA Button - Tag Style */
.article-cta {
  margin: 0.25rem 0 2rem 0;
  text-align: left;
}

.article-cta-button {
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;
  background: rgba(87, 188, 235, 0.15);
  color: #57bceb;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.875rem;
  padding: 0.5rem 0.875rem;
  border-radius: 12px;
  border: 1px solid rgba(87, 188, 235, 0.3);
  transition: all 0.2s ease;
  position: relative;
}

.article-cta-button:hover {
  background: rgba(87, 188, 235, 0.25);
  border-color: rgba(87, 188, 235, 0.5);
  color: #ffffff;
  transform: translateY(-0.5px);
  box-shadow: 0 2px 8px rgba(87, 188, 235, 0.3);
}

.article-cta-button:active {
  transform: translateY(0);
  background: rgba(87, 188, 235, 0.3);
}

.cta-arrow {
  width: 14px;
  height: 14px;
  transition: transform 0.2s ease;
  opacity: 0.8;
}

.article-cta-button:hover .cta-arrow {
  transform: translateX(1px);
  opacity: 1;
}

/* Mobile CTA Button */
@media (max-width: 600px) {
  .article-cta {
    margin: 0.875rem 0 1.5rem 0;
    text-align: left;
  }
  
  .article-cta-button {
    font-size: 0.85rem;
    padding: 0.45rem 0.75rem;
    gap: 0.35rem;
  }
  
  .cta-arrow {
    width: 13px;
    height: 13px;
  }
}

/* Scroll-triggered animations */
.animate-on-scroll {
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: transform, opacity;
  backface-visibility: hidden;
}

.animate-on-scroll.visible {
  opacity: 1;
  will-change: auto;
}

.slide-up {
  transform: translateY(30px);
}

.slide-up.visible {
  transform: translateY(0);
}

/* Mobile-specific animation adjustments */
@media (max-width: 767px) {
  .animate-on-scroll {
    transition-duration: 0.4s;
  }
  
  .slide-up {
    transform: translateY(20px);
  }
}

/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  .animate-on-scroll {
    transition: none !important;
    animation: none !important;
    transform: none !important;
    opacity: 1 !important;
  }
}